.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0,0,0,.5);
  display: flex;
  justify-content: center;
  padding-top: 5rem;
}


.modal-body {
  width: 400px;
  padding: 2rem;
  border-radius: 10px;
  background: #fff;
  height: 200px;
}